.dialogs {
  position: fixed;
  display: none;
  opacity: 0;
  width: 100%;
  height: 100%;
  z-index: 50;
  overflow-x: hidden;
  -webkit-overflow-scrolling: touch;
  background: var(--toner-160);
  backdrop-filter: blur(24px);
  top: 0;
  left: 0;
}
.dialogs .close-bg {
  position: fixed;
  z-index: 51;
  width: 100%;
  height: 100%;
}
.dialogs .flex-popup {
  display: flex;
  flex-direction: column;
  justify-content: center;
  min-height: 100%;
  width: 100%;
  box-sizing: border-box;
}
.dialogs .flex-popup.popup--call .popup {
  animation: upCall 1600ms ease forwards;
}
@keyframes upCall {
  0% {
    opacity: 0;
    transform: translate3d(100%, -100vh, 0) scale(0);
  }
  50% {
    opacity: 0.3;
    transform: translate3d(0, 0, 0) scale(0.5);
  }
  100% {
    opacity: 1;
    transform: translate3d(0, 0, 0) scale(1);
  }
}
.dialogs .flex-popup.popup--policy-order .popup__wrapper {
  width: 800px;
  max-width: 800px;
  min-height: 372px;
}
.dialogs .flex-popup.popup--more-detail .popup__wrapper {
  width: 800px;
  max-width: 800px;
  min-height: 372px;
}
.dialogs .flex-popup.popup--cart {
  align-items: flex-start;
  justify-content: flex-start;
  flex-direction: row-reverse;
}
.dialogs .flex-popup.popup--cart .popup {
  position: absolute;
  top: 0;
  right: -100%;
  margin: 0;
  transition: right 300ms;
}
.dialogs .flex-popup.popup--cart .popup.active {
  right: 0;
  margin: 4px 4px 4px 0 !important;
}
.dialogs .flex-popup.popup--cart .popup__wrapper {
  width: 368px;
  height: calc(100vh - 8px);
  transform: translate3d(0, 0, 0) scale(1);
}
.dialogs .flex-popup.popup--cart .popup__title {
  display: flex;
  align-items: center;
  margin: 0 20px 0 20px;
  padding-bottom: 22px;
}
.dialogs .flex-popup.popup--cart .popup__content {
  height: calc(100vh - 8px);
  padding: 24px 4px 4px 4px;
}
.dialogs .flex-popup.popup--list {
  align-items: flex-start;
  justify-content: flex-end;
  width: 100%;
  overflow: hidden;
}
.dialogs .flex-popup.popup--list .popup {
  position: absolute;
  left: 0;
  bottom: -100%;
  max-width: 100%;
  min-width: 100%;
  margin: 0 auto;
  transition: bottom 300ms;
}
.dialogs .flex-popup.popup--list .popup.active {
  bottom: 0;
  margin: 0;
  padding: 4px;
}
.dialogs .flex-popup.popup--list .popup__wrapper {
  width: 100%;
  max-width: 100%;
  min-width: 100%;
  height: calc(100vh - 236px);
  transform: translate3d(0, 0, 0) scale(1);
}
.dialogs .flex-popup.popup--list .popup__title {
  display: flex;
  align-items: center;
  margin: 0 22px 0 22px;
  padding-bottom: 22px;
}
.dialogs .flex-popup.popup--list .popup__content {
  padding: 24px 4px 4px 4px;
}
.dialogs .flex-popup.popup--details .popup {
  position: absolute;
  left: 0;
  top: -100%;
  max-width: 100%;
  min-width: 100%;
  margin: 0 auto;
}
.dialogs .flex-popup.popup--details .popup.active {
  top: 200px;
  margin: 0;
  padding: 4px;
}
.dialogs .flex-popup.popup--details .popup__wrapper {
  width: 100%;
  max-width: 800px;
  transform: none;
}
.dialogs .flex-popup.popup--details .popup__content {
  width: 100%;
  height: 100%;
  max-height: 100%;
  min-height: 100%;
  padding: 64px 80px;
}
.dialogs .flex-popup.popup--gallery .popup {
  max-width: 100%;
  min-width: 100%;
  margin: 0 auto;
  transform: translate3d(0, 0, 0) scale(0.1);
}
.dialogs .flex-popup.popup--gallery .popup.active {
  margin: 0;
  padding: 0;
  transform: translate3d(0, 0, 0) scale(1);
}
.dialogs .flex-popup.popup--gallery .popup__wrapper {
  width: 100%;
  max-width: 100%;
  transform: none;
  border-radius: 0;
}
.dialogs .flex-popup.popup--gallery .popup__content {
  width: 100%;
  height: 100%;
  max-height: 100%;
  min-height: 100%;
  padding: 0;
}
.dialogs .close {
  z-index: 53;
}
.dialogs .close:before, .dialogs .close:after {
  background: var(--white);
}
.dialogs .popup {
  display: none;
  opacity: 0;
  margin: 0 auto;
  position: relative;
  z-index: 52;
  width: 100%;
  max-width: max-content;
  border: none;
  padding: 0;
  box-sizing: border-box;
  transform: translate3d(0, -30px, 0) scale(0.9);
  transition: all ease-in-out 0.3s;
  background: transparent;
  color: var(--black);
}
