.dialogs .popup .close {
  position: absolute;
  right: 24px;
  top: 24px;
  display: flex;
  align-items: center;
  justify-content: center;
  width: 24px;
  height: 24px;
  border-radius: 50%;
  background: linear-gradient(90deg, rgba(46, 46, 46, 0.5) 0%, rgba(69, 69, 69, 0.5) 100%);
