.popup {
  background-color: transparent;
}
.popup__wrapper {
  display: flex;
  flex-direction: row;
  flex-wrap: nowrap;
  justify-content: space-between;
  width: 480px;
  min-height: 372px;
  margin: 0 auto;
  border-radius: 16px;
  background-color: var(--white);
  position: relative;
  z-index: 2;
  max-width: 480px;
  padding: 0;
  box-sizing: border-box;
  overflow: visible;
}
.popup__content {
  display: flex;
  flex-direction: column;
  width: 100%;
  padding: 48px 80px;
}
.popup__content h3 {
  color: var(--black);
  text-align: center;
}
.popup__form {
  display: flex;
  flex-direction: column;
  width: 100%;
  margin-top: 32px;
  gap: 24px;
}
.popup__file {
  gap: 4px;
  width: 100%;
  padding: 17px 0;
  border-radius: 4px;
  color: var(--gray);
  outline: 1px dashed var(--gray-3);
  transition: all ease-in-out 0.2s;
  cursor: pointer;
}
.popup__file > span {
  font-size: 10px;
  line-height: 140%;
}
.popup__file:hover {
  outline-color: var(--black);
}